import React, { Component } from 'react';
import { Tabs, Tab, Grid, Cell, Card, CardTitle, CardText, CardActions, Button, CardMenu, IconButton } from 'react-mdl';
import Y1 from './materials/Y1';
import Y11 from './materials/Y11';
import Y21 from './materials/Y21';
import Y31 from './materials/Y31';
import Y41 from './materials/Y41';
import Y42 from './materials/Y42';
import Y43 from './materials/Y43';

class EEE extends Component {
  constructor(props){
    super(props);
    this.state = { activeTab:0 };
  }
  toggleCategories(){
    if(this.state.activeTab===0){
      return(
        <Y1 />
      )
    } else if(this.state.activeTab===1){
        return(
          <Y21 />
        )
      } else if(this.state.activeTab===2){
          return(
            <Y31 />
        )
      } else if(this.state.activeTab===3){
            return(
              <Y41 />
        )
      }
    }
      toggleCategories2(){
        if(this.state.activeTab===0){
          return(
              <div></div>
          )
        } else if(this.state.activeTab===1){
            return(
              <div></div>
            )
          } else if(this.state.activeTab===2){
              return(
                <div></div>
            )
          } else if(this.state.activeTab===3){
                return(
                  <Y42 />
            )
          }

  }
  toggleCategories3(){
    if(this.state.activeTab===0){
      return(
        <div></div>
      )
    } else if(this.state.activeTab===1){
        return(
          <div></div>
        )
      } else if(this.state.activeTab===2){
          return(
            <div></div>
        )
      } else if(this.state.activeTab===3){
            return(
              <Y43 />
        )
      }

}

	render(){
		return(
			<div className="category-tabs">
        <Tabs activeTab={this.state.activeTab} onChange={(tabId) => this.setState({ activeTab: tabId })} ripple>
          <Tab>Year 1</Tab>
          <Tab>Year 2</Tab>
          <Tab>Year 3</Tab>
          <Tab>Year 4</Tab>
        </Tabs>

          <Grid className="subject-grid">
            <Cell col={12}>
              <div className="content">{this.toggleCategories()}</div>
            </Cell>
          </Grid>
          <Grid className="subject-grid2">
            <Cell col={12}>
              <div className="content">{this.toggleCategories2()}</div>
            </Cell>
          </Grid>
          <Grid className="subject-grid3">
            <Cell col={12}>
              <div className="content">{this.toggleCategories3()}</div>
            </Cell>
          </Grid>
      </div>
		)
	}
}

export default EEE;
