import React, { Component } from 'react';
import { Grid, Cell, Button } from 'react-mdl';


class Contact extends Component {

  render() {

    return (
			<div>
				<h1>Your Feedback / Updates</h1>
      			<form action="https://formspree.io/bigkusdickgus@gmail.com" method="POST">
							<div className="feedback-form">
        				<textarea
								autoFocus
	          		className="text-input"
	          		id="feedback-entry"
	          		name="feedback-entry"
	          		//onChange={this.handleChange}
	          		placeholder="Please provide me updates when new materials are released. You may drop me an email through this form, or send me a dropbox/google drive link"
	          		required
	          		//value={this.state.feedback}
	        		/>
							</div>
	        		<div className="btn-group">
			          <Button raised>
			          	<input type="submit"
													value="Send"
													className="btn btn--submit" />
									</Button>
	        		</div>
						</form>
					</div>
    );
  }
}

export default Contact;
